import React, { useEffect } from 'react';
import PropTypes from 'prop-types';
import { RestourantCard } from '../RestourantCard';
import './RestourantsListPage.scss';

const DEFAULT_ETA_RANGE = '20 - 30 min';

export const RestaurantsListPage = ({ restaurantsData, loadRESTAURANTS }) => {
  useEffect(() => {
    loadRESTAURANTS();
  }, []);

  return (
    <div className="restaurants-list">
      {restaurantsData.map(
        (restaurant) => {
          const {
            uuid,
            title,
            heroImageUrl: imageUrl,
            categories,
            etaRange,
          } = restaurant;

          return (
            <RestourantCard
              key={uuid}
              uuid={uuid}
              title={title}
              imageUrl={imageUrl}
              categories={categories}
              etaRange={etaRange ? etaRange.text : DEFAULT_ETA_RANGE}
            />
          );
        }
      )}
    </div>
  );
};

RestaurantsListPage.propTypes = {
  restaurantsData: PropTypes.arrayOf(PropTypes.shape({

  })),
  loadRESTAURANTS: PropTypes.func.isRequired,
};

RestaurantsListPage.defaultProps = {
  restaurantsData: [],
};
